.hotelBlock {
  padding: 32px;

  width: 664px;
  height: 894px;

  background: #ffffff;

  box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
  border-radius: 16px;

  .header {
    display: flex;
    justify-content: space-between;
    .description {
      display: flex;
      justify-content: space-between;
      .arrowBlock {
        display: flex;
        align-items: center;
        margin: 0px 15px;
        .arrow {
          width: 15px;
          height: 15px;
        }
      }
    }
    .date {
      display: flex;
      align-items: center;
    }
  }
  .slider {
    display: flex;
  }
  .favourites {
    display: flex;
    justify-content: flex-start;
  }
}
